import { classToPlain, plainToClass } from 'class-transformer';
import { AuditFields } from './AuditFields';

export class PlatformInfo {
    id: string;
    name: string;
    description: string;
    linkText: string;
    link: string;

    logoUrl: string;
    auditTrail!: AuditFields;
    constructor() {
        this.id = "";
        this.name = "";
        this.description = "";
        this.logoUrl = "";
        this.linkText = "";
        this.link = "";

    }
    static converter = {
        toFirestore(expense: PlatformInfo): firebase.firestore.DocumentData {
            return classToPlain(expense);
        },
        fromFirestore(
            snapshot: firebase.firestore.QueryDocumentSnapshot,
            options: firebase.firestore.SnapshotOptions
        ): PlatformInfo {
            const data = snapshot.data(options)!;
            return plainToClass(PlatformInfo, data);
        }
    };
}